import '../App.css'
import React, {useState} from 'react'
import LoginService from '../services/Auth'
import md5 from 'md5'
import PasswordChecklist from "react-password-checklist"

const Login = ({setIsPositive, setMessage, setShowMessage, setLoggedInUser}) => {

// Komponentin tilan määritys
const [username, setUsername] = useState('')
const [password, setPassword] = useState('')
const [passwordAgain, setPasswordAgain] = useState("")


// onSubmit tapahtumankäsittelijä funktio
const handleSubmit = (event) => {
      event.preventDefault()
      var userForAuth = {
        username: username,
        password: md5(password) // Salataan md5 kirjaston metodilla       
    }

    // Käytetään services/Auth.js tiedoston metodia
    LoginService.authenticate(userForAuth)
    .then(response => {
        if (response.status === 200) {
     
        // Talletetaan tietoja selaimen local storageen (f12 application välilehti)
        localStorage.setItem("username", response.data.username)
        localStorage.setItem("accesslevelId", response.data.accesslevelId)
        localStorage.setItem("token", response.data.token)
        
        // Asetetaan app komponentissa olevaan stateen
        setLoggedInUser(response.data.username)

       setMessage(`Logged in as: ${userForAuth.username}`)
       setIsPositive(true)
       setShowMessage(true)
      
       setTimeout(() => {
        setShowMessage(false)
       }, 5000)

    }
      })
      .catch(error => {
        setMessage(error.message)
        setIsPositive(false)
        setShowMessage(true)

        setTimeout(() => {
          setShowMessage(false)
         }, 6000)
      })
    }

    // Kenttien tyhjennys
    const emptyFields = () => {
        setUsername("")
        setPassword("")
    } 


  return (
    <div id="loginWindow">
      <hr></hr>
      <br></br>
       <h3>Login</h3>
       
       <form onSubmit={handleSubmit}>
            <div>
                <input type="text" value={username} placeholder="Username"
                    onChange={({ target }) => setUsername(target.value)} />
            </div>
            <div>
                <input type="password" value={password} placeholder="Password"
                    onChange={({ target }) => setPassword(target.value)} />
            </div>
            <div>
                <input name="confirmPassword" type="password" value={passwordAgain} placeholder="Confirm Password"
                    onChange={({ target }) => setPasswordAgain(target.value)} />
            
                 <PasswordChecklist 
				            rules={["minLength","match"]}
				            minLength={4}
				            value={password}
				            valueAgain={passwordAgain}
				            messages={{
                      minLength: "Password has at least 4 characters.",
                      match: "Password verification.",
                    }}
                    />             
                  
            </div>
            
         <input type='submit' value='Login' />
         <input type='button' value='Empty' onClick={() => emptyFields()} />
       </form>
       <br></br>
       <br></br>
       <hr></hr>

    </div>
  )
}

export default Login
